@use 'global' as * ; 
.pc-only {
  display: none;

  @include mq(tab) {
    display: block;
  }
}

.sp-only {
  @include mq(tab) {
    display: none !important;
  }
}

.u-hidden-pc {
  // PC時に非表示

  @include mq("pc") {
    display: none;
  }
}

.u-hidden-sp {
  // スマホ時に非表示

  @include mq("sp") {
    display: none;
  }
}
