@use 'global' as * ; 
.p-works__title {
  position: relative;

  @include mq(tab) {
    letter-spacing: 0.0167em;
  }
}

.p-works__title::before {
  content: "Collection";
  display: block;
  font-size: rem(50);
  font-weight: normal;
  position: absolute;
  top: rem(-49);
  left: 50%;
  transform: translateX(-50%);
  color: $title-color;
  font-family: $font-family-sub;
  z-index: -1;

  @include mq(tab) {
    left: 74%;
    font-size: rem(110);
    top: rem(-34);
    letter-spacing: 0.0182em;
  }
}

.p-works__content {
  margin-top: rem(48);
  background: $bg-sub-color;
  padding-bottom: rem(22);

  @include mq(tab) {
    margin-top: rem(32);
    background: linear-gradient(
      to right,
      transparent 30.5%,
      $bg-sub-color 30.5%
    );
    width: 100%;
    padding-top: rem(80);
    padding-bottom: rem(80);
  }
}

.p-works__inner {
  @include mq(tab) {
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-left: rem(5);
    width: 100%;
    max-width: rem(1110);
  }
}

.p-works__img-box {
  opacity: 0;
  height: 60vw;
  max-height: rem(420);

  @include mq(tab) {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: rem(557);
    height: 35vw;
  }
}

.p-works__img-box.is-animated {
  animation-name: slideLeft; /*1で解説*/
  animation-fill-mode: forwards; /*開始と終了の状態*/
  animation-duration: 0.5s; /*アニメーションの長さ*/
  animation-timing-function: linear; /*アニメーションの進行具合*/
  animation-delay: 0.5s; /*アニメーションの開始を遅らせる*/
  animation-direction: normal; /*アニメーションの向き*/
}

.p-works__img-box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 20%;

  @include mq(tab) {
    max-width: rem(557);
  }
}

.p-works__text-box {
  width: 100%;
  padding-left: rem(21);
  padding-right: rem(21);
  margin-top: rem(21);

  @include mq(tab) {
    flex: 0 1 rem(574);
    margin-top: 0;
    padding-left: rem(32);
    padding-right: rem(0);
  }
}

.p-works__sub-title {
  font-size: rem(20);
  letter-spacing: 0.1em;
  text-align: center;

  @include mq(tab) {
    text-align: left;
    font-size: rem(25);
    letter-spacing: 0.02em;
  }
}

.p-works__text {
  margin-top: rem(24);
  letter-spacing: 0.125em;
  line-height: calc(23 / 16);

  @include mq(tab) {
    margin-top: rem(35);
    font-size: rem(16);
    line-height: calc(35 / 16);
    letter-spacing: 0.0313em;
  }
}

.p-works__btn {
  margin-top: rem(26);
  text-align: center;

  @include mq(tab) {
    margin-top: rem(50);
    text-align: left;
  }
}

.p-works__swiper {
  height: inherit;
  max-height: rem(420);

  .swiper-container img {
    width: 100%;
    @include mq(tab) {
      max-width: rem(557);
    }
  }

  .swiper-wrapper {
    width: 100%;
    @include mq(tab) {
      max-width: rem(557);
    }
  }

  .swiper-slide {
    width: 100%;
    @include mq(tab) {
      max-width: rem(557);
    }
  }
}
