@use 'global' as * ; 
.p-mv {
  width: 100%;
  height: 100vh;
  position: relative;
}

.p-mv__img {
  width: 100%;
  height: 100vh;
  position: relative;
}

.p-mv__img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.p-mv__content {
  width: 100%;
  text-align: center;
  position: absolute;
  width: 100%;
  max-width: rem(280);
  padding: rem(20) 0;
  top: 70%;
  left: 40%;
  transform: translate(-50%, -50%);
  z-index: 100;
  color: #fff;

  @include mq(tab) {
    left: 30%;
    max-width: rem(550);
  }
}

.p-mv__content::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: $main-color;
  position: absolute;
  top: 0;
  left: 0;
  clip-path: polygon(100% 0, 0 0, 0 100%, 0 0);

  animation-name: clip-2;
  animation-fill-mode: forwards; /*開始と終了の状態*/
  animation-duration: 0.5s; /*アニメーションの長さ*/
  animation-timing-function: linear; /*アニメーションの進行具合*/
  animation-delay: 2s; /*アニメーションの開始を遅らせる*/
}

.p-mv__title {
  font-size: rem(28);
  font-weight: 700;
  line-height: 1;

  @include mq(tab) {
    font-size: rem(50);
  }
}

.p-mv__title span {
  display: inline-block;
  letter-spacing: -0.2em;
}

.p-mv__title span:not(:first-child) {
  margin-left: rem(5);
  @include mq(tab) {
    margin-left: rem(15);
  }
}

.p-mv__title span:first-child {
  opacity: 0;
  animation-name: slideUp;
  animation-fill-mode: forwards; /*開始と終了の状態*/
  animation-duration: 0.3s; /*アニメーションの長さ*/
  animation-timing-function: ease; /*アニメーションの進行具合*/
  animation-delay: 1s; /*アニメーションの開始を遅らせる*/
}

