@use "global" as *;
.p-news__inner {
  width: 100%;
  max-width: rem(1100);
  margin: 0 auto;
  padding-left: rem(20);
  padding-right: rem(20);

  @include mq(tab) {
    padding-left: rem(27);
    padding-right: rem(31);
  }
}

.p-news__items {
  @include mq(tab) {
    display: flex;
  }
}

.p-news__items-inner {
  width: 100%;
}

.p-news__item + .p-news__item {
  margin-top: rem(32);
}

.p-news__btn {
  margin-top: rem(28);
  text-align: center;
  font-size: rem(20);
  letter-spacing: 0.2em;
  line-height: 1;
  max-width: rem(335);
  margin-left: auto;
  margin-right: auto;

  @include mq(tab) {
    margin-top: rem(3);
    margin-left: rem(30);
    margin-right: 0;
    width: rem(134);
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: rem(16);
    letter-spacing: 0.001em;
    align-self: flex-start;
  }
}
