  margin-top: 0.8125rem;
  letter-spacing: 0.0625em;
}

.breadcrumbs {
  font-size: 0.875rem;
}
@media screen and (min-width: 768px) {
  .breadcrumbs {
    font-size: 1rem;
  }
}

.c-btn {
  display: inline-block;
  font-size: 1.25rem;
  letter-spacing: 0.1em;
  line-height: 1;
  padding: 0.875rem 0;
  text-align: center;
  width: 100%;
  max-width: 20.9375rem;
  border: 1px solid #FFEA2E;
  background: #FFEA2E;
  overflow: hidden;
  transition: all 0.3s;
}
@media screen and (min-width: 768px) {
  .c-btn {
    font-size: 1rem;
    letter-spacing: 0.125em;
    max-width: 22.5rem;
    padding: 1.3125rem 0;
  }
}

.c-btn--news {
  display: inline-block;
  padding: 0.875rem 0;
  text-align: center;
  width: 100%;
  border: 1px solid #111;
  background: #FFEA2E;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .c-btn--news {
    padding: 0.3125rem 0;
    transition: all 0.3s;
  }
}

.c-btn--slide {
  overflow: hidden;
}

.c-btn--slide a {
  position: relative;
  z-index: 2;
  border: 1px solid #fff;
}
@media screen and (min-width: 768px) {
  .c-btn--slide a {
    border: none;
  }
}

.c-btn--slide a:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #e99bc1;
  transition: all 300ms linear;
}

@media screen and (min-width: 768px) {
  .c-btn--slide a:hover {
    color: #fff;
  }
}

@media screen and (min-width: 768px) {
  .c-btn--slide a:hover:before {
    left: 0;
  }
}

.c-btn--slide--white {
  overflow: hidden;
}

.c-btn--slide--white a {
  position: relative;
  z-index: 2;
}

.c-btn--slide--white a:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #e99bc1;
  border: 1px #e99bc1;
  transition: all 300ms linear;
}

@media screen and (min-width: 768px) {
  .c-btn--slide--white a:hover {
    color: #fff;
    border: 1px solid #e99bc1;
  }
}

@media screen and (min-width: 768px) {
  .c-btn--slide--white a:hover:before {
    left: 0;
  }
}

.c-btn--header {
  background: #FFEA2E;
  color: #333333;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
