}

.c-hamburger {
  z-index: 9999;
}
@media screen and (min-width: 768px) {
  .c-hamburger {
    display: none;
  }
}

.c-hamburger span {
  position: relative;
  display: block;
  height: 2px;
  width: 26px;
  background: #111;
  -webkit-transition: ease 0.3s;
  transition: ease 0.3s;
}

.c-hamburger span:nth-child(1) {
  top: 0;
}

.c-hamburger span:nth-child(2) {
  margin: 6px 0;
}

.c-hamburger span:nth-child(3) {
  top: 0;
}

/*OPEN時の動き*/
.c-hamburger.open span:nth-child(1) {
  top: 8px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.c-hamburger.open span:nth-child(2) {
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  opacity: 0;
}

.c-hamburger.open span:nth-child(3) {
  top: -8px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

body {
  opacity: 0;
  animation: loader-fadeOut 2s;
  animation-fill-mode: both;
  animation-timing-function: ease; /*アニメーションの進行具合*/
}

@keyframes loader-fadeOut {
  100% {
    opacity: 1;
  }
}
.c-loader__top {
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background: #e99bc1;
  z-index: 9998;
  animation-name: clip-1;
  animation-fill-mode: forwards; /*開始と終了の状態*/
  animation-duration: 1s; /*アニメーションの長さ*/
  animation-timing-function: linear; /*アニメーションの進行具合*/
