@use "global" as *;
.p-overview {
  position: relative;
}

.p-overview.is-animated::before {
  content: "";
  display: block;
  width: 100%;
  height: rem(800);
  background: url(../img/common/itimatu-pink.jpg) no-repeat center center /
    cover;
  clip-path: polygon(100% 0, 100% 0, 100% 0, 100% 0);
  position: absolute;
  top: rem(-240);
  left: 0%;
  z-index: -2;

  animation-name: borderSlide2; /*1で解説*/
  animation-fill-mode: forwards; /*開始と終了の状態*/
  animation-duration: 1.5s; /*アニメーションの長さ*/
  animation-timing-function: linear; /*アニメーションの進行具合*/
  animation-direction: normal; /*アニメーションの向き*/
  animation-delay: 1.5s; /*アニメーションの開始を遅らせる*/

  @include mq(tab) {
    height: rem(1380);
    top: rem(-390);
  }
}

.p-overview__title {
  position: relative;
  @include mq(tab) {
    letter-spacing: 0.0167em;
  }
}

.p-overview__title::before {
  content: "Overview";
  display: block;
  font-size: rem(50);
  font-weight: normal;
  position: absolute;
  top: rem(-49);
  left: 50%;
  transform: translateX(-50%);
  color: $title-color;
  font-family: $font-family-sub;
  z-index: -2;

  @include mq(tab) {
    left: 20%;
    font-size: rem(110);
    top: rem(-32);
    letter-spacing: 0.0182em;
  }
}

.p-overview__content {
  margin-top: rem(46);
  background: $bg-sub-color;
  padding-bottom: rem(22);

  @include mq(tab) {
    margin-top: rem(34);
    background: linear-gradient(to left, transparent 30%, $bg-sub-color 30%);
    width: 100%;
    padding-top: rem(80);
    padding-bottom: rem(80);
  }
}

.p-overview__inner {
  @include mq(tab) {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-left: rem(4);
    width: 100%;
    max-width: rem(1110);
  }
}

.p-overview__img {
  width: 100%;
  position: relative;
  opacity: 0;

  @include mq(tab) {
    // height: rem(510);
    display: flex;
    align-items: center;
    width: 100%;
    max-width: rem(557);
  }
}

.p-overview__img.is-animated {
  animation-name: slideRight; /*1で解説*/
  animation-fill-mode: forwards; /*開始と終了の状態*/
  animation-duration: 0.5s; /*アニメーションの長さ*/
  animation-timing-function: linear; /*アニメーションの進行具合*/
  animation-delay: 0.5s; /*アニメーションの開始を遅らせる*/
  animation-direction: normal; /*アニメーションの向き*/
}

.p-overview__img::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba($color: #111111, $alpha: 0.2);
}

.p-overview__img::before {
  padding-top: 69.6%; //画像の縦 261/ 375横
  display: block;
  content: "";
}

.p-overview__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
  vertical-align: bottom;
  @include mq(tab) {
    max-width: rem(557);
  }
}

.p-overview__text-box {
  width: 100%;
  margin-top: rem(21);
  padding-left: rem(21);
  padding-right: rem(21);

  @include mq(tab) {
    margin-top: rem(0);
    flex: 0 1 rem(574);
    padding-left: rem(10);
    padding-right: rem(32);
  }
}

.p-overview__sub-title {
  font-size: rem(20);
  letter-spacing: 0.1em;
  text-align: center;

  @include mq(tab) {
    text-align: left;
    font-size: rem(25);
    letter-spacing: 0.02em;
  }
}

.p-overview__text {
  margin-top: rem(24);
  line-height: calc(23 / 16);
  letter-spacing: 0.125em;

  @include mq(tab) {
    margin-top: rem(37);
    font-size: rem(16);
    line-height: calc(35 / 16);
    letter-spacing: 0.0313em;
  }
}

.p-overview__btn {
  margin-top: rem(25);
  text-align: center;

  @include mq(tab) {
    margin-top: rem(48);
    text-align: left;
    border: none;
  }
}
