@use 'global' as * ; 
.p-lower-content-body__block {
  @include mq(tab) {
    display: flex;
    width: 100%;
  }
}

.p-lower-content-body__block + .p-lower-content-body__block {
  margin-top: rem(55);
  @include mq(tab) {
    margin-top: rem(101);
  }
}

.p-lower-content-body__img {
  width: 100%;
  max-width: rem(540);
  margin-left: auto;
  margin-right: auto;
  position: relative;

  @include mq(tab) {
    flex: 0 1 rem(550);
    max-width: rem(550);
  }
}

.p-lower-content-body__img::before {
  padding-top: 69%; //画像の横 261/374 縦
  display: block;
  content: "";

  @include mq(tab) {
    padding-top: 68%;
  }
}

.p-lower-content-body__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}

.p-lower-content-body__title-box {
  padding: rem(24) 0;
  max-width: rem(540);
  width: 100%;

  @include mq(tab) {
    flex: 0 1 rem(564);
    max-width: rem(564);
    padding: rem(20);
    margin-left: rem(30);
  }
}

.p-lower-content-body__block:nth-child(even) {
  @include mq(tab) {
    flex-direction: row-reverse;
  }
  .p-lower-content-body__title-box {
    @include mq(tab) {
      margin-right: rem(30);
    }
  }
}

.p-lower-content-body__title {
  font-size: rem(20);
  letter-spacing: 0.1em;
  text-align: center;
  background-image: radial-gradient(#7bded962 30%, transparent 30%);
  background-size: 20px 20px;
  padding: rem(20);
  color: $accent-color;
  font-weight: 700;

  @include mq(tab) {
    font-size: rem(25);
    letter-spacing: 0.08em;
  }
}

.p-lower-content-body__sub-title {
  font-size: rem(16);
  margin-top: rem(10);
}

.p-lower-content-body__price {
  font-size: rem(16);
  text-align: center;
}

.p-lower-content-body__price span {
  font-size: rem(30);
  color: $accent-color;
  margin-left: rem(10);
}

.p-lower-content-body__text-box {
  margin-top: rem(20);
  padding: rem(10);
  border: 5px solid $main-color;
  position: relative;
}

.p-lower-content-body__text-box::before {
  content: "セット内容";
  display: inline-block;
  font-size: rem(16);
  padding: 0 rem(5);
  color: $main-color;
  background-color: #fff;
  position: absolute;
  top: rem(-16);
  left: 50%;
  transform: translateX(-50%);

  @include mq(tab) {
    padding: 0 rem(10);
    font-size: rem(20);
    top: rem(-20);
  }
}

.p-lower-content-body__text-box:nth-child(2):before {
  content: "成人式当日";
}
.p-lower-content-body__text-box:nth-child(3)::before {
  content: "前撮り撮影会";
}

.p-lower-content-body__text {
  display: inline-block;
  margin-right: rem(10);
  font-size: rem(16);
  letter-spacing: 0.125em;
  line-height: 1.45;

  @include mq(tab) {
    letter-spacing: 0.034em;
    line-height: 1.6;
    margin-right: rem(20);
  }
}

.p-lower-content-body__text-box:nth-child(2),
.p-lower-content-body__text-box:nth-child(3) {
  .p-lower-content-body__text {
    display: block;
    margin-right: initial;
  }
}
